import React from "react";
import style from "./Header.module.scss";
import MenuIcon from '@mui/icons-material/Menu';
import MenuOpenIcon from '@mui/icons-material/MenuOpen';
import logo from "../../assets/png/logo.png";
import {Link} from "react-router-dom";
import {useAppDispatch, useAppSelector} from "../../store/hooks";
import {selectBurgerMenu, setBurgerMenu} from "../../store/appSlice";
import {IconButton} from "@mui/material";

export const links = [
    {label: "Characters", to: "/characters"},
    {label: "Locations", to: "/locations"},
    {label: "Episodes", to: "/episodes"},
    {label: "Products", to: "/products"},
];


export const Header = () => {
    const burgerMenu = useAppSelector(selectBurgerMenu);
    const dispatch = useAppDispatch()
    const onBurger = () => dispatch(setBurgerMenu({burgerMenu: !burgerMenu}));

    return (
        <header className={style.header}>
            <div className={style.inner}>
                <Link className={style.logo}
                      to="/"
                >
                    <img src={logo} alt=""/>
                </Link>

                <nav className={style.links}>
                    {
                        links.map(({label, to}, index) => (
                            <Link key={index} to={to} className={style.link}>
                                <span>{label}</span>
                            </Link>
                        ))
                    }
                </nav>

                <IconButton  className={style.burger}
                             onClick={onBurger}
                >
                    {burgerMenu ? <MenuOpenIcon/> : <MenuIcon/>}
                </IconButton>
            </div>
        </header>
    )
}