import {createSlice, PayloadAction} from "@reduxjs/toolkit";
import {RootState} from "./store";

const initialState = {
    burgerMenu: false,
}

export type InitialStateType = typeof initialState

export const appSlice = createSlice({
    name: "app",
    initialState: initialState as InitialStateType,
    reducers: {
        setBurgerMenu: (state, action: PayloadAction<{burgerMenu: boolean}>) => {
            state.burgerMenu = action.payload.burgerMenu
        },
    }
})

export const {
    setBurgerMenu
} = appSlice.actions

export const selectBurgerMenu = (state: RootState) => state.app.burgerMenu

export const appReducer = appSlice.reducer